import {
  required,
  validEmail,
  minLength6,
  maxLength15,
  validString,
  validNumber,
  validDob,
  validID,
  requiredSelect,
  checkClassTypeId
} from "utils/validate";
import { buildErr } from "utils/utils";
import history from "state/history";
import store from "state/store";

export const require = value =>
  required(value)
    ? undefined
    : buildErr("y l trng bt buc, vui lng khng b trng");

export const email = value =>
  validEmail(value) ? undefined : buildErr("Email khng hp l");

export const password = value => {
  // console.log(value);
  if (minLength6(value) && maxLength15(value)) {
    return undefined;
  } else {
    return buildErr("Mt khu khng hp l");
  }
};

export const validName = value =>
  validString(value) ? undefined : buildErr("Vui lng nhp h tn hp l");

export const validPhone = value =>
  validNumber(value)
    ? undefined
    : buildErr("Vui lng nhp s in thoi hp l");

export const validD = value =>
  validDob(value) ? undefined : buildErr("Vui lng nhp ngy sinh hp l");

export const matchPassword = (value, allValues) =>
  value !== allValues.password
    ? buildErr("Mt khu khng trng khp. Vui lng nhp li")
    : undefined;

export const idValid = value =>
  validID(value) ? undefined : buildErr("Vui lng nhp s CMND hp l");

export const requireSelect = value =>
  requiredSelect(value)
    ? undefined
    : buildErr("y l trng bt buc, vui lng khng b trng");
